<template>
    <div class="selectbox">
        <div class="select-selection">
          <span @click="open" id="selectText">{{text}}</span>
        </div>
        <div class="lk_down" id="down" style="display:none;top:-1px;">
          <ul>
            <li class="select-item" v-for="i in items" :class="{'select-item-selected':text=== i.label}" @click="selectClick(i.label)" >{{i.label}}</li>
          </ul>
        </div>
    </div>
</template>
<script>
export default{
  name:'selectbox',
  props:{
    items:Array,
    spanText:String
  },
  data(){
    return {
      text:'男生'
    }
  },

  methods:{
    open(){
      var div = document.getElementById('down');
      div.style.display = 'block';
    },
    selectClick(value){
      this.text = value;
      var div = document.getElementById('down');
      div.style.display = 'none';

    }
  }
}
</script>
<style>
 ul {
  margin:0;
  padding:0;
 }
.selectbox{
    float:left;
    padding-left: 1rem;
    width: 3rem;
    margin: 0.5rem 0;
  }
  .lk_down {
    max-height: 200px;
    overflow: auto;
    margin: 5px 0;
    padding: 5px 0;
    background-color: #fff;
    box-sizing: border-box;
    border-radius: 4px;
    box-shadow: 0 1px 6px rgba(0,0,0,.2);
    position: absolute;
    z-index: 900;
  }
  .lk_down ul{
    color:#555;
    width:4.5rem;
    padding-left: 0!important;
    text-align: left;
  }
  .select-item {
    margin: 0;
    line-height: normal;
    padding: 7px 12px;
    clear: both;
    color: #495060;
    font-size: 12px!important;
    white-space: nowrap;
    list-style: none;
    cursor: pointer;
    transition: background .2s ease-in-out;
  }
  .select-selection {
    display: block;
    box-sizing: border-box;
    outline: 0;
    -webkit-user-select: none;
    cursor: pointer;
    position: relative;
    background-color: #fff;
    border-radius: 4px;
    border: 1px solid #dddee1;
    transition: all .2s ease-in-out;
  }

  .select-selection span {
    display: block;
    height: 22px;
    line-height: 22px;
    font-size: 12px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .select-item-selected {
    color: #fff;
    background: rgba(45,140,240,.9)
  }
</style>


