<template>
  <div class="choice">
    <div class="hg">
      <h4>精选</h4>
      <Select :text="selectValue" :items="gender"></Select>
    </div>
    <div style="margin-top: 40px;">
      <div class="page-swipe">
        <mt-swipe :auto="4000" class="mt-swipe">
          <mt-swipe-item class="mt-swipe-item"><img src="../assets/1.jpg"></mt-swipe-item>
          <mt-swipe-item class="mt-swipe-item"><img src="../assets/2.jpg"></mt-swipe-item>
          <mt-swipe-item class="mt-swipe-item"><img src="../assets/3.jpg"></mt-swipe-item>
          <mt-swipe-item class="mt-swipe-item"><img src="../assets/4.jpg"></mt-swipe-item>
          <mt-swipe-item class="mt-swipe-item"><img src="../assets/5.jpg"></mt-swipe-item>
        </mt-swipe>
      </div>
      <div class="nav">
        <div class="item" v-for="(n,index) in navList">
          <router-link :to="n.name">
            <i class="mintui" :class="[n.icon]"></i>
            <div class="title">{{n.title}}</div>
          </router-link>
        </div>
      </div>
      <div class="broad">
        <span><i class="mintui mintui-guangbo"></i>我举报! 假! 太假了! 我的世界是个假的!</span>
      </div>
      <div class="contents">
        <div class="cnt" @click="more('畅销精选')">
          <h4>畅销精选</h4>
          <span>更多<i class="mintui mintui-gengduo1"></i></span>
        </div>
        <div @click="goDetails(b)" class="books" v-for="(b,index) in bookList" v-if="index < 3">
          <div class="bLeft">
             <img :src="b.path" class="sImg">
          </div>
          <div class="sug tf">
            <div>{{b.name}}</div>
            <div class="word">{{b.synopsis}}</div>
            <div class="wsp">
              <span>
                <i class="mintui mintui-zuozhe"></i>{{b.author}}
              </span>
              <span class="sca">
                <span>{{b.type}}</span>
                <span>连载</span>
                <span>25万字</span>
              </span>
            </div>
          </div>
        </div>
      </div>
      <div class="contents">
        <div class="cnt" @click="more('主编力荐')">
          <h4>主编力荐</h4>
          <span>更多<i class="mintui mintui-gengduo1"></i></span>
        </div>
        <div @click="goDetails(b)" class="books" v-for="(b,index) in bookList" v-if="index /2 >3">
          <div class="bLeft">
             <img :src="b.path" class="sImg">
          </div>
          <div class="sug tf">
            <div>{{b.name}}</div>
            <div class="word">{{b.synopsis}}</div>
            <div class="wsp">
              <span>
                <i class="mintui mintui-zuozhe"></i>{{b.author}}
              </span>
              <span class="sca">
                <span>{{b.type}}</span>
                <span>连载</span>
                <span>25万字</span>
              </span>
            </div>
          </div>
        </div>
      </div>
      <div class="contents">
        <div class="cnt" @click="more('免费专区')">
          <h4>免费专区</h4>
          <span>更多<i class="mintui mintui-gengduo1"></i></span>
        </div>
        <div class="boxs bx">
          <div @click="goDetails(b)" class="box" v-for="(b,index) in bookList" v-if="index /1 >5">
            <div >
               <img :src="b.path" class="sImg">
            </div>
            <div>
              {{b.name}}
            </div>
            <div class="small">
              5点千字
            </div>
            <div class="cr">
              免费
            </div>
          </div>
        </div>
      </div>
      <div class="contents">
        <div class="cnt" @click="more('新书强推')">
          <h4>新书强推</h4>
          <span>更多<i class="mintui mintui-gengduo1"></i></span>
        </div>
        <div @click="goDetails(b)" class="books" v-for="(b,index) in bookList" v-if="index /2 >3">
          <div class="bLeft">
             <img :src="b.path" class="sImg">
          </div>
          <div class="sug tf">
            <div>{{b.name}}</div>
            <div class="word">{{b.synopsis}}</div>
            <div class="wsp">
              <span>
                <i class="mintui mintui-zuozhe"></i>{{b.author}}
              </span>
              <span class="sca">
                <span>{{b.type}}</span>
                <span>连载</span>
                <span>25万字</span>
              </span>
            </div>
          </div>
        </div>
      </div>
      <div class="contents">
        <div class="cnt" @click="more('经典完本')">
          <h4>经典完本</h4>
          <span>更多<i class="mintui mintui-gengduo1"></i></span>
        </div>
        <div @click="goDetails(b)" class="books" v-for="(b,index) in bookList" v-if="index /2 >3">
          <div class="bLeft">
             <img :src="b.path" class="sImg">
          </div>
          <div class="sug tf">
            <div>{{b.name}}</div>
            <div class="word">{{b.synopsis}}</div>
            <div class="wsp">
              <span>
                <i class="mintui mintui-zuozhe"></i>{{b.author}}
              </span>
              <span class="sca">
                <span>{{b.type}}</span>
                <span>连载</span>
                <span>25万字</span>
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import {Indicator} from 'mint-ui';
  import Select from '@/components/plug/select.vue';
  export default {
    name:'choice',
    components:{
      Select
    },
    data(){
      return {
        gender: [
          {value:'1',label:'男生'},
          {value:'2',label:'女生'}
        ],
        selected:'1',
        selectValue:'男生',
        navList:[
          {name:'rankingList',title:'排行榜',icon:'mintui-paixangbang'},
          {name:'classify',title:'分类',icon:'mintui-fenlei'},
          {name:'threeRiver',title:'三江榜',icon:'mintui-bangdan'},
          {name:'',title:'专题',icon:'mintui-zhuanti'}
        ],
        bookList:[]
      }
    },
    methods:{
      goDetails(obj){
        console.log(obj)
        this.$router.push({
          name:'/bookDetails',params:{name:obj.name},
        });
      },
      more(name){
        this.$router.push({
          name:'/list',params:{name:name},meta:{title:name}
        })
      }
    },
    created(){
      // Indicator.open('加载中');
      this.$axios.post('/vueBook/bookList',{})
      .then((res)=>{
        if(res.data.state === 200){
          // Indicator.close();
          this.bookList = res.data.data;
        }
      })
      .catch((err)=>{
        console.log(err)
      });
    },
    mounted(){
      var body = document.querySelector('body');
      var div = document.getElementById('down');
      body.addEventListener('click',(e)=>{
        if(e.target.id === 'selectText'){
          div.style.display = 'block';
        }else {
          div.style.display = 'none';
        }
      });
    },

  }
</script>
<style scoped>
  .choice {
    margin-bottom: 3.2rem;
    overflow: hidden;
    font-family: SimSun;
    background-color: #ddd;
  }
  .hg{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    width: 100%;
    height: 40px;
    background: #E10601;
    line-height: 38px;
  }
  .hg h4 {
    float: left;
    padding-left: 3rem;
    color:#fff;
  }

  .mt-swipe {
    height: 120px
  }
  .mt-swipe-item {
    line-height: 120px;
  }
  .mt-swipe-item img{
    height: 120px;
    width:100%;
  }

  .nav {
    display: flex;
    justify-content: space-around;
    padding:1rem 0;
    background-color: #fff;
  }
  .item {
    width: 5rem;
  }
  .item a {
    font-size: 16px;
    text-decoration: none;
    font-family: 宋体;
    color:#000;
  }
  .item i {
    font-size: 30px;
    color:red;
  }
  .item .title {
    line-height: 26px;
  }

  .broad {
    font-size:12px;
    background: #fff;
  }
  .broad span {
    display: inline-block;
    padding: 0.5rem 1.5rem 0.5rem 1rem;
    background-color:#f9f9f9;
    border:1px solid #ddd;
    border-radius:1rem;
    letter-spacing: 1px;
    -webkit-transform: scale(0.9);
  }
  .broad i{
    padding-right: 0.5rem;
  }
  .contents {
    margin-bottom: 0.5rem;
    background-color: #fff;
  }
  .cnt {
    overflow: hidden;
    padding:1rem 1rem 0.5rem;
  }
  .cnt h4{
    float: left;
    padding-left: 1rem;
    border-left: 2px solid red;
  }
  .cnt span {
    float: right;
    font-size: 14px;
    color: #777;
  }
  .cnt i{
    font-size: 12px;
    padding-left: 5px;
  }

</style>


